* {
  box-sizing: border-box;
}

h1, h2, p, body {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo__image {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto-Medium, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: block;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 > :nth-child(-n+1), .stars--2 > :nth-child(-n+2), .stars--3 > :nth-child(-n+3), .stars--4 > :nth-child(-n+4), .stars--5 > :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-flow: column wrap;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .5s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__item {
  box-sizing: border-box;
  flex-direction: column;
  width: 100%;
  display: flex;
}

.card__item-name {
  color: #060b35;
  margin-bottom: 4px;
  font-family: Roboto-Medium, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__item-code {
  color: #616070;
  height: 14px;
  margin-bottom: 16px;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__item-name {
  color: #34568b;
}

.card__review {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 19px;
  width: 100%;
  height: 16px;
  margin-bottom: 24px;
  padding: 0;
  display: flex;
}

.card__review-value {
  text-align: right;
  justify-content: center;
  align-items: center;
  height: 16px;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-bottom: 16px;
  display: flex;
}

.card__price-text {
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  text-align: right;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 18px;
}

.card__buy-button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 11px 0;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.content {
  flex-wrap: wrap;
  grid-template: auto auto / auto auto auto auto;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 1023px) {
  .content {
    grid-template: auto auto / auto auto auto;
  }
}

@media (width <= 767px) {
  .content {
    grid-template: auto auto / auto auto;
  }
}

@media (width <= 487px) {
  .content {
    grid-template: auto auto / auto;
  }
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Regular.ca197847.ttf");
}

@font-face {
  font-family: Roboto-Medium;
  src: url("Roboto-Medium.91322885.ttf");
}

